import React from "react"
import { createUseStyles } from "react-jss";
import '../../pages/styles.css'
import {useWindowSize} from "../../hooks/useWindowSize";
import gato from '../../imgs/drips.jpg'
import Link from "gatsby-link";

const useStyles = createUseStyles({
  container: {
  },
});

const Layout = ({children, path}) => {
  const {width} = useWindowSize()
  const classes = useStyles();
  console.log(path, '-')
  
  return (
    <div className={classes.container}>
      {path !== '/' && <Link to='/' style={{position: 'fixed', top: 0, left: 0}}>
        <img style={{width: 30}} src={gato} alt=""/>
      </Link>}
      <div style={{
        position: 'fixed',
        top: 500,
        left: width > 500 ? -90 : -88,
        transform: 'rotate(270deg)',
        color: 'red',
        zIndex: 3,
        overflow:'hidden'
      }}>
        <div className="marqueeright">
            <p style={{padding: 0}}>
              sitio x <a style={{color: 'blue'}} href="https://instagram.com/maximocampo">@maximocampo</a>
            </p>
          </div>
        </div>
      {children}
    </div>
  )
};

export default Layout
